<template>
  <el-row class="form-bg">
    <el-col :span="10" :offset="6">
      <el-form ref="form" :rules="rules" :model="forminfo" label-width="100px">
        <el-form-item label="活动名称" prop="title">
          <el-input v-model="forminfo.title"></el-input>
        </el-form-item>
        <el-form-item label="活动时间">
          <el-date-picker
            v-model="time"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        
        <el-form-item label="一级分类"  prop="first_cateid">
          <el-select @change="topChange(forminfo.first_cateid)" v-model="forminfo.first_cateid" placeholder="请选择">
              <el-option
                v-for="item in catelist"
                :key="item.id"
                :label="item.catename"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="二级分类"  prop="second_cateid">
            <el-select @change="secondChange(forminfo.second_cateid)"  v-model="forminfo.second_cateid" placeholder="请选择">
              <el-option
                v-for="item in second_list"
                :key="item.id"
                :label="item.catename"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="选择商品"  prop="goodsid">
            <el-select v-model="forminfo.goodsid" placeholder="请选择">
              <el-option
                v-for="item in goodlist"
                :key="item.id"
                :label="item.goodsname"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import { addSeckill, getSeckillList, editSeckill } from "@/request/seckill";
import { getGoodsArr } from "@/request/seckill";
import { mapGetters,mapActions }  from "vuex"
import $axios from "@/common/http"
export default {
  data() {
    return {
      time:[],
      second_list: [], // 二级分类
      goodlist:[],
      forminfo: {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: "",
        status: 1, // 状态，1是正常，2是禁用
      },
      rules:{
      }
    };
  },
  computed: {
    ...mapGetters({
      catelist:"category/catelist"
    })
  },
  async mounted() {
    if(!this.catelist.length){
      await this.getlist();
    }
    this.$nextTick(async ()=>{
      // 如果是修改页面，就请求当前页面的数据！
      if (this.$route.params.id) {
        await this.getinfo();
      }
    })
  },
  methods: {
      ...mapActions({
        getlist:"category/getlist"
      }),
      topChange(vid) {
        this.second_list = []
        this.catelist.forEach( val=> {
            if(val.id==vid && val.children){
                this.second_list = val.children
            }
        });
      },
      secondChange(vid){
         if(!vid){
            return
         }
          $axios.get('/goodslist', {
              params:{fid:this.forminfo.first_cateid, sid:vid}
          }).then(res=>{
            if (res.list) {
               this.goodlist = res.list
            }
          })
      },
    async getinfo() {
      this.forminfo = await getSeckillList(this.$route.params.id);
      this.forminfo.begintime = new Date(this.forminfo.begintime*1) 
      this.forminfo.endtime = new Date(this.forminfo.endtime*1) 
      this.time = [this.forminfo.begintime,this.forminfo.endtime]
      this.topChange(this.forminfo.first_cateid)
      this.secondChange(this.forminfo.second_cateid)
    },
    submit() {
      // 验证
      // 如果文件信息和字段信息一起提交，那么需要使用FormData对象！
      if(this.time.length){
        this.forminfo.begintime = this.time[0].getTime();
        this.forminfo.endtime = this.time[1].getTime();
      }else{
          this.$message.warning('请选择时间');
          return;
      }
      this.$refs.form.validate(valid=>{
        if(valid){
          // 提交数据
          if (this.$route.params.id) {
            editSeckill(this.$route.params.id,this.forminfo);
          } else {
            addSeckill(this.forminfo);
          }
        }else{
            this.$message.warning('验证失败')
        }
      })
    },
    reset(){
      if (this.$route.params.id) {
        this.getinfo();
      }else{
        this.$refs.form.resetFields();
      }
    }
  },
  components: {},
};

// BootStrap
</script>
<style scoped>
</style>


