import $axios from "@/common/http"
import axios from "axios"
import router from "@/router"

import { $Message } from "@/common/utils"

// 增删改查数据请求！


// 获取数据
export async function getSeckillList(id) {
    let res;
    if (id) {
        res = await $axios.get('/seckinfo?id=' + id);
    } else {
        res = await $axios.get('/secklist');
    }
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}



/**
 * 增加数据
 * @param {*} data   添加的数据
 */
export async function addSeckill(data) {
    let res = await $axios.post('/seckadd', data);
    if (res.code == 200) {
        $Message('success', '添加成功', () => {
            router.back();
        })
    }
}



/**
 * 修改数据
 * @param {*} id   修改id
 * @param {*} data   修改的数据
 */
export async function editSeckill(id, data) {
    data.append('id', id)
    let res = await $axios.post('/seckedit', data);
    if (res.code == 200) {
        $Message('success', '修改成功', () => {
            router.back();
        })
    }
}


/**
 * 删除数据
 * @param {*} id 删除id
 */
export async function delSeckill(id) {
    let res = await $axios.post('/seckdelete', { id });
    if (res) {
        $Message('success', '删除成功')
    }
}


